import React, { useEffect, useRef } from 'react'
import menfas from '../components/img/categories/clothes-svgrepo-com.svg'
import womfas from '../components/img/categories/dress-svgrepo-com.svg'
import jwe from '../components/img/categories/two-rings-svgrepo-com.svg'
import electronics from '../components/img/categories/laptop-svgrepo-com.svg'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'

function Sidemenu(props) {
    const showMenu = props.showMenu
    const sidemenu = useRef();
    const {currentCat} = useSelector((state) => state.login)
    useEffect(() => {
        if (showMenu) {
            sidemenu.current.classList.add("fullHeight")
        }
        else {
            sidemenu.current.classList.remove("fullHeight")
        }
    }, [showMenu])

  return (
    <div ref={sidemenu} className='sidemenu'>
        <div className="sidemenu_wrapper">
            <div className={`category ${currentCat === "men's clothing" ? "active" :''}`}>
                <Link to="/category/men's clothing">
                    <img src={menfas} alt="mens fashion pic"/> men&lsquo;s fashion
                </Link>
            </div>
            <div className={`category ${currentCat === "women's clothing" ? "active" :''}`}>
                <Link to="/category/women's clothing">
                    <img src={womfas} alt="womens fashion pic"/>women&lsquo;s fashion
                </Link>
            </div>
            <div className={`category ${currentCat === "jewelery" ? "active" :''}`}>
                <Link to="/category/jewelery">
                    <img src={jwe} alt="jwellery pic" /> Jewellery
                </Link>
            </div>
            <div className={`category ${currentCat === "electronics" ? "active" :''}`}>
                <Link to="/category/electronics">
                    <img src={electronics} alt="Category icon" />electronics
                </Link>
            </div>
        </div>

    </div>
  )
}

export default Sidemenu;