import {createSlice} from '@reduxjs/toolkit'
import { login } from './thunks';
import { verifyOnMount, onRegister, onCartAdd, removeCartItem, onfavAdd, removeFavItem} from './thunks';
import { clacCartTotal } from '../functions/calcCartTotal';

const loginSlice = createSlice({
    name:'login',
    initialState: {
        isloading: false,
        status : false,
        userid: '',
        token: '',
        errmsglog: '',
        errmsgreg: '',
        cart: [],
        cartTotal: 0,
        fav: [],
        currentCat: null,
        searchTerm: ''
    },
    reducers: {
        logout: (state) => {
            localStorage.setItem("access_token", '')
            state.status = false
            state.token = ''
            state.userid = ''
        },
        highlight: (state, action) => {
            state.currentCat = action.payload
        },
        setSearchTerm: (state, action) => {
            // if (!action.payload) {
            //     state.searchTerm = null
            //     return
            // }
            state.searchTerm = action.payload
        }
    },
    extraReducers: {
        [login.pending]: (state, action) => {
            console.log("pending")
        },
        [login.fulfilled]: (state, action) => {
            if (action.payload.message === "Incorrect email or password") {
                state.isloading = false;
                state.status = false
                state.errmsglog = action.payload.message
                state.errmsgreg = ''
                return
            }
            console.log(action.payload)
            state.isloading = false;
            state.status = true;
            const token = action.payload.access_token
            localStorage.setItem("access_token", token)
            state.token = token
            state.userid = action.meta.arg.email
            state.cart = action.payload.info.cart
            state.fav = action.payload.info.fav
            state.cartTotal = clacCartTotal(action.payload.info.cart)
        },
        [login.rejected]: (state, action) => {
            state.isloading = false;
        },
        [verifyOnMount.pending]: (state, action) => {
            state.isloading = true;
        },
        [verifyOnMount.fulfilled]: (state, action) => {
            console.log(action)
            if (action.payload.status === 'token expired') {
            state.isloading = false;
            state.status = false
            console.log("when expired")
            }
            else if (action.payload.status === 'ok') {
                state.isloading = false;
                state.status = true
                state.userid = action.payload.email
                state.token = action.meta.arg
                state.cart = action.payload.info.cart
                state.fav = action.payload.info.fav
                console.log("when valid", action.payload)
                state.cartTotal = clacCartTotal(action.payload.info.cart)
            }
            else {
                console.log("something else nwated happend at verifyOnMount.fulfilled")
            }
        },
        [verifyOnMount.rejected]: (state, action) => {
            state.isloading = false;
            state.status = false
        },
        [onRegister.pending]: (state, action) => {
            
        },
        [onRegister.fulfilled]: (state, action) => {
            console.log(action.payload)
            if (action.payload.status) {
                console.log('new user created')
                console.log(action.payload.access_token)
                state.status = true
                state.token = action.payload.access_token
                state.userid = action.payload.userid
                localStorage.setItem("access_token", action.payload.access_token)
                state.errmsgreg = ''
                state.errmsglog = ''
                return
            }
            state.errmsgreg = action.payload.msg
            state.errmsglog = ''
        },
        [onRegister.rejected]: (state, action) => {
            console.log("onRegister rejected")
        },
        [onCartAdd.fulfilled]: (state, {payload}) => {
            if (payload.status === "ok") {
                state.cart.push(payload.item)
                state.cartTotal = state.cartTotal + payload.item.price
                return
            }
            else {
                console.log("onCartAdd fulfilled", "token expired logging out")
                state.status= false
                state.userid = null
                state.token = null
                state.cart = null
                state.fav = null
                state.cartTotal = null
            }
        },
        [removeCartItem.fulfilled]: (state, {payload}) => {
            if (payload.status === 'ok') {
                console.log("payload", payload)
                console.log(state.cart)
                const newCart = state.cart.filter(item => item.id !== payload.item.id)
                console.log(newCart)
                state.cart = newCart
                state.cartTotal = state.cartTotal - payload.item.price
            }
            else {
                state.status= false
                state.userid = null
                state.token = null
                state.cart = null
                state.fav = null
                state.cartTotal = null
            }
        },
        [onfavAdd.fulfilled]: (state, {payload}) => {
            if (payload.status === "ok") {
                state.fav.push(payload.item)
            }
            else {
                state.status= false
                state.userid = null
                state.token = null
                state.cart = null
                state.fav = null
                state.cartTotal = null
            }
            console.log("onCartAdd fulfilled", payload)
        },
        [removeFavItem.fulfilled]: (state, {payload}) => {
            if (payload.status === 'ok') {
                console.log(payload)
                const newFav = state.fav.filter(item => item.id !== payload.id)
                state.fav = newFav
            }
            else {
                state.status= false
                state.userid = null
                state.token = null
                state.cart = null
                state.fav = null
                state.cartTotal = null
            }
        },
    }
}
)


export const { logout, highlight, setSearchTerm} = loginSlice.actions;
export default loginSlice.reducer;
