import React, { useState, useEffect } from 'react'
import Slider1 from './Slider1';
import { callApi } from '../CallApi';
import Slider_small from './Slider_small';
import offerImg from '../img/special-offer.png'
import ProductList from '../ProductList/ProductList';
import Modal from '../Modal';

function Homepage() {
  const [list, setList] = useState()
  useEffect(() => {
    callApi("/products", setList)
  }, [])

  if (!list) {
    return (
      <div className='homepage'>
      <div className="slider1">
        <Slider1 />
      </div>
    </div>
    )
  }

  console.log(list)
  const slider_list = list.slice(16, list.length)
  console.log(slider_list)
  const hotItem = list[12]
  const newProducts = list.slice(12, list.length)
  
  return (
    <div className='homepage'>
      <div className="slider1">
        <Slider1 />
      </div>
      <div className="popular-items">
        <Slider_small list={slider_list}/>
      </div>
      <div className="hot_product">
        <div className="row gx-6">
          <div className="col-12 col-md-4 d-flex justify-content-center img-col">
            <div className="img-wrapper">
              <div className="discount-overlay">
                15 %
              </div>
              <img src={offerImg} alt="special offer" className='offer-img'/>
              <img src={hotItem.image} alt="hot-item-image"  className='hot-item-image'/>
            </div>
          </div>
          <div className="col-12 col-md-8 pb-5 py-md-5 px-5 px-md-0">
            <div className="price"><span><del>$&nbsp;{hotItem.price + 101}</del></span>$&nbsp;{hotItem.price}</div>
            <div className="title">{hotItem.title}</div>
            <div className="offer-msg">
              15 % off <br/>
              Limited Time Offer
            </div>
          </div>
        </div>
      </div>
      <div className="cupon">
        <div>Super discount for your <strong>first purchase.</strong></div>
        <div className="cuponCode">FREE25BAC</div>
        <div className="info">Use discount code on checkout!</div>
      </div>
      <div className="new-products">
        <header>
          <div className="left">
            <div>NEW PRODUCTS</div>
            <div>New products with updated stocks.</div>
          </div>
          <div>
            <div className="right">View All <i class="bi bi-arrow-right"></i></div>
          </div>
        </header>
        <ProductList list={newProducts}/>
      </div>
    </div>
    
  )
}

export default Homepage;