import React, { useEffect, useState } from 'react'
import {useDispatch, useSelector} from 'react-redux'
import { Link } from 'react-router-dom'
import { verifyOnMount } from '../redux/thunks'

function Userinfo() {
    const dispatch = useDispatch()
    const {status, cartTotal, cart, isLoading} = useSelector((state) => state.login)
    const [cartItemsNo, setCartItemsNo] = useState(null)
    useEffect(() => {
        const token = localStorage.getItem("access_token");
        dispatch(verifyOnMount(token))
    }, [])
    function clacNo () {
      let num = 0
      cart.map(() => {
         num++
      })
      return num
    }
    function cartlist () {
      let list = cart
      if (!cart) {
        return (
          <div className="no-item">
            No items in cart!
          </div>
        )
      }
      if (cart.length > 3) {
        list = cart.slice(2)
      }
      if (!status) {
        return (
          <div className="no-list">
            Please log in!
          </div>
        )
      }
      if (list.length < 1) {
        return (
          <div className="no-list">
            No Items Added!
          </div>
        )
      } 
      const renderedList = list.map((item) => {
        return (
          <div className="item row justify-content-between">
            <div className="col-5">
              <Link to='/profile'>
                <img src={item.image} alt="product image" className='w-100'/>
              </Link>
            </div>
            <div className="col-7">
              <Link to='/profile'>
                {item.title}
              </Link>
            </div>
          </div>
        ) 
      })
      console.log(renderedList)
      return renderedList
    }

  return (
    <div className='nav-profile'>
      <Link to='/profile'>
        <i className="bi bi-person"></i>
      </Link>
      <Link to='/profile'>
        <span className='cart-amt'>{status ?"$ " +cartTotal : "Sign in"}</span>
      </Link>
      <Link to='/profile' className='cart-wrapper'>
        <i className="bi bi-cart4"><span className='smallnum'>{clacNo()}</span></i>
        <div className="hover-list">
            {cartlist()}
        </div>
      </Link>
    </div>
  )
}

export default Userinfo