import React from 'react'
import ReactDOM from 'react-dom';
import {Link} from 'react-router-dom'

function Modal({isOpen, message1, message2, btnMsg, onCancel}) {
    console.log("modal", isOpen)
    if (isOpen) {
        
        return ReactDOM.createPortal(
            
            <>
                <div className="modal-wrapper" onClick={onCancel}>
                    <div className="diolouge-box" onClick={e => e.stopPropagation()}>
                        <div className="notice">{message1}</div>
                        <div className='modal-msg'>{message2}</div>
                        <Link to="/profile"><button className='modal-btn'>{btnMsg}</button></Link>
                        <div className='x-wrapper' onClick={onCancel}>
                            <i class="bi bi-x"></i>
                        </div>
                    </div>
                </div>
            </>
        , document.getElementById('modal'))
    }
    return null
}

export default Modal