import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import ProductList from '../ProductList/ProductList'
import { setSearchTerm } from '../redux/slice'

function Search() {
    const {term} = useParams()
    // const term = useSelector((state) => state.login.searchTerm)
    const [list, setList] = useState(null)
    // const [serchedTerm, setSearchedTerm] = useState(null)
    const dispatch = useDispatch()
    useEffect(() => {

      const callLocalApi = async(endpoint, setState, term) => {
        const baseurl = "http://localhost:3000/products"
        const options = {
            method: "POST",
            body: JSON.stringify({term: term}),
            headers: {
                'Content-Type': 'application/json'
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            mode: "cors"
        }
        const res = await fetch(baseurl+endpoint, options)
        const json = await res.json()
        console.log(json)
        setState(json)
      }
      
        callLocalApi('/search', setList, term)
        dispatch(setSearchTerm(term))
      
      return () => {
        console.log('unounted')
        dispatch(setSearchTerm(''))
      }
    },[term])

    if (!list) {
      return (
        <div className="loading">loading...</div>
      )
    }
    if (list.status) {
      return (
        <div className="search-body-wrapper">
          <div className="toplabel">
            Search &gt; &nbsp; {term}
          </div>
          <div className="info">
            <span>Showing result for:</span> {term}
          </div>
          <div className="product-list">
            <ProductList list={list.result} banner={true}/>
          </div>
        </div>
      )
    }
    if (!list.status) {
      return (
        <div className="search-body-wrapper">
          <div className="toplabel">
            Search &gt; &nbsp; {term}
          </div>
          <div className="info no-result">
            <i class="bi bi-info-circle-fill"></i>
            <span>No results found for:</span> {term}
          </div>
        </div>
      )
    }
    return (
      <div>Error Shoud not happen</div>
    )
}

export default Search