import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { logout } from '../redux/slice'
import {removeCartItem, removeFavItem} from '../redux/thunks'

function Dashbord() {
    const {cart, fav, userid, token} = useSelector((state) => state.login)
    const dispatch = useDispatch()
    console.log("userid : "+ userid)
    // if (!userid) {
    //     return <div className="loading">Loading...</div>
    // }
    function removeCart (id) {
      console.log("removecart pressed")
      dispatch(removeCartItem({id, token}))
    }
    function removeFav (id) {
      console.log("removeFav pressed")
      dispatch(removeFavItem({id, token}))
    }
    const renderedCart = cart.map((item) => {
      return (
        <div className="item" key={item.id}>
          <div className="cancel" onClick={() => removeCart(item.id)}><i class="bi bi-x"></i></div>
          <div><img src={item.image} alt="Item-Image" /></div>
          <div>{item.title}</div>
          <div><del>$&nbsp;{(item.price+15).toFixed(2)}</del><span>$&nbsp;{item.price}</span></div>
        </div>
      )
    })
    const renderedFav = fav.map((item) => {
      return (
        <div className="item" key={item.id}>
          <div className="cancel" onClick={() => removeFav(item.id)}><i class="bi bi-x"></i></div>
          <div><img src={item.image} alt="Item-Image" /></div>
          <div>{item.title}</div>
          <div><del>$&nbsp;{(item.price+15).toFixed(2)}</del><span>$&nbsp;{item.price}</span></div>
        </div>
      )
    })
  return ( 
    <div className="dashboard">
        <div className="toplabel">Home &#62;&nbsp; Profile &#62;&nbsp; Dashborad</div>
        <div className="content">
            <div className='intro'>Hello <strong>{userid}</strong> &nbsp;&nbsp;(<em>not {userid}?</em><span className='link' onClick={() => dispatch(logout())}>Log out</span>)</div>
            <div className="row">
              <div className="cart col-12 col-md-6">
                <div className="head">Cart Items</div>
                <div className="content">
                  {renderedCart}
                </div>
              </div>
              <div className="fav col-12 col-md-6">
                  <div className="head">Liked Items</div>
                  <div className="content">
                    {renderedFav}
                  </div>
              </div>
            </div>
            <button className='login-btn' onClick={() => dispatch(logout())}>Logout</button>
        </div>
    </div>
  )
}

export default Dashbord