import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { useParams } from 'react-router-dom'
import { callApi } from '../CallApi'
import ProductList from '../ProductList/ProductList'
import {highlight} from '../redux/slice'

function Category() {
    const {cat} = useParams()
    const [list, SetList] = useState()
    const dispatch = useDispatch()
    useEffect(() => {
        callApi("/products/category/"+cat, SetList)
        dispatch(highlight(cat))
        //clean up
        return () => dispatch(highlight(null))
    }, [cat])
    if (!list) {
        return <div className="loading">Loading...</div>
    }
  return (
        <div className="body-wrapper">
            <div className="toplabel">Products 	&gt;&nbsp; {cat} </div>
            <div className="product-list">
                <ProductList list={list} banner={true}/>
            </div>
        </div>
    )
}

export default Category