import React, { useRef, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import svg from '../../components/img/secure.svg'
import logofull from '../../components/img/bacola-logo-full.png'
import logomob from '../../components/img/bacola-logo-mobile.png'
import menfas from '../../components/img/categories/clothes-svgrepo-com.svg'
import womfas from '../../components/img/categories/dress-svgrepo-com.svg'
import jwe from '../../components/img/categories/two-rings-svgrepo-com.svg'
import electronics from '../../components/img/categories/laptop-svgrepo-com.svg'
import Userinfo from './Userinfo'
import { useDispatch, useSelector } from 'react-redux'
import { setSearchTerm } from '../redux/slice'

function Navbar(props) {
  const [showMobMenu, setShowMobMenu] = useState(true);
  const [showHamMenu, SetShowHamMenu] = useState(false)
  const ref1 = useRef();
  const ref2 = useRef();
  const navigate = useNavigate()
  const {currentCat, searchTerm} = useSelector((state) => state.login)
  const dispatch = useDispatch()
  
  const onallcatClick = () => {
    if (props.showMenu === true) {
      props.setShowMenu(false)
    }
    else {
      props.setShowMenu(true)
    }
  }
  const onallcatClick2 = () => {
    console.log(ref1.current)
    if (showMobMenu === true) {
      ref1.current.classList.add('fullHeight')
      setShowMobMenu(false)
    }
    else {
      ref1.current.classList.remove('fullHeight')
      setShowMobMenu(true)
    }
  }
  const onMenuClick = () => {
    console.log("clickde")
    if (showHamMenu === true) {
      SetShowHamMenu(false)
      ref2.current.classList.remove('show')
    }
    else {
      console.log(ref2)
      SetShowHamMenu(true)
      ref2.current.classList.add('show')
    }
  }
  function onSearchSumbit (e) {
    e.preventDefault()
    navigate(`/search/${searchTerm}`)
  }
  function onSearchSumbitMob (e) {
    e.preventDefault()
    navigate(`/search/${searchTerm}`)
    onMenuClick()
  }
  return (
    <div className="navbarr">
      {/* Mobile side toggle menu */}
      <div className="mobmenu row g-0" ref={ref2}>
        <div className="col-10 col-lg-5 col-md-6 visible">
          <i className="bi bi-x" onClick={onMenuClick}></i>
          <div className="moblogo" onClick={onMenuClick}>
            <Link to="/">
              <img src={logofull} alt="Logo"/>
            </Link>
          </div>
          <div className="allcat-btn col-3" onClick={() => { onallcatClick2() }}>

            <i className="bi bi-list"></i> <span className='all-txt'>ALL CATEGORIES</span> <i className="bi bi-chevron-down"></i>
          </div>
          <div className="sidemenu_wrapper" ref={ref1}>
              <div className='pad'>
                <div className={`category ${currentCat === "men's clothing" ? "active" :''}`} onClick={onMenuClick}>
                    <Link to="/category/men's clothing">
                        <img src={menfas} alt="mens fashion pic"/> men&lsquo;s fashion
                    </Link>
                </div>
                <div className={`category ${currentCat === "women's clothing" ? "active" :''}`} onClick={onMenuClick}>
                    <Link to="/category/women's clothing">
                        <img src={womfas} alt="womens fashion pic"/>women&lsquo;s fashion
                    </Link>
                </div>
                <div className={`category ${currentCat === "jewelery" ? "active" :''}`} onClick={onMenuClick}>
                    <Link to="/category/jewelery">
                        <img src={jwe} alt="jwellery pic" /> Jewellery
                    </Link>
                </div>
                <div className={`category ${currentCat === "electronics" ? "active" :''}`} onClick={onMenuClick}>
                    <Link to="/category/electronics">
                        <img src={electronics} alt="Category icon" />electronics
                    </Link>
                </div>
              </div>
          </div>
          {/* mob serach bar */}
          <div className="mobile-searchbox">
            <div className="searchbox-wrapper mobile">
              <form onSubmit={onSearchSumbitMob}>
                <input type="text" className='searchbox' onChange={(e) => dispatch(setSearchTerm(e.target.value))} value={searchTerm} />
                <i className="bi bi-search" onClick={onSearchSumbit}></i>
              </form>
            </div>
          </div>
          {/* mob serach bar */}
          <div className='mob-item' onClick={onMenuClick}><Link to="/">Home</Link></div>
          <div className='mob-item' onClick={onMenuClick}><Link to="/aboutus">About Us</Link></div>
          <div className='mob-item' onClick={onMenuClick}><Link to="/profile">Wishlist</Link></div>
          <div className='mob-item' onClick={onMenuClick}><Link to="/profile">My account</Link></div>
        </div>
        <div className="col-2 col-md-6 col-lg-7 black-overlay" onClick={onMenuClick}></div>
      </div>
      {/* Mobile side toggle menu */}
      <div className="bluebar">
        <div className="container text-light text-center p-1">Due to the <span className='bold'>COVID 19</span> epidemic, orders may be processed with a slight delay</div>
      </div>
      <div className="infobar d-none d-lg-block">
        <div className="container">
          <div className="row justify-content-between align-items-center">
            <div className="col-3 links">
              <Link to="/">Home</Link>
              <Link to="/profile">My account</Link>
              <Link to="/aboutus">About Us</Link>
            </div>
            <div className="col-5 text-center"><img src={svg} alt="hand washing icon" />100% Secure delivery without contacting the courier</div>
            <div className="col-3 text-end"> Need help? Call Us: + 0020 500</div>
          </div>
        </div>
      </div>
      <div className="logobar">
        <div className="container">
          <div className="row justify-content-between align-items-center">
            <div className='d-lg-none col-2'>
              <i className="bi bi-list" onClick={onMenuClick}></i>
            </div>
            <div className='d-none d-lg-block col-3'>
              <Link to="/">
                <div><img src={logofull} alt="logo" className='w-100 logo-full' /></div>
                <div className='logotext d-none d-lg-block'>Online Grocery Shopping Center</div>
              </Link>
            </div>
            <div className="col-6 d-none d-lg-block searchbox-wrapper">
              <form onSubmit={onSearchSumbit}>
                <input type="text" className='searchbox' onChange={(e) => dispatch(setSearchTerm(e.target.value))} value={searchTerm}/>
                <i className="bi bi-search" onClick={onSearchSumbit}></i>
              </form>
            </div>
            <div className="col-3 d-lg-none">
              <Link to="/">
                <img src={logomob} alt="mobile logo" className='w-100'/>
              </Link>
            </div>
            <div className="col-3 col-sm-4 col-lg-2 p-0">
              <Userinfo/>
            </div>
          </div>
        </div>
      </div>
      <div className="nav-links d-none d-lg-block">
        <div className="container">
          <div className="row justify-content-between">
            <div className="allcat-btn col-3" onClick={() => { onallcatClick() }}>
              <i className="bi bi-list"></i> <span className='all-txt'>ALL CATEGORIES</span> <i className="bi bi-chevron-down"></i>
            </div>
            <div className="links col-8">
              <div className='links-wrapper'>
                <span>
                  <Link to='/'>
                    Home
                  </Link>
                </span>
                <span>
                  <Link to="/category/men's clothing">
                    Men's Fashion <i className="bi bi-chevron-down"></i>
                  </Link></span>
                <span>
                  <Link to="/category/women's clothing">
                    Women's Fashion
                  </Link></span>
                <span>
                  <Link to="/category/jewelery">
                    Jewellery
                  </Link>
                </span>
                <span>
                  <Link to="/category/electronics">
                    Electronics
                  </Link>
                </span>
                <span>
                  <Link to="/aboutus">
                    About Us
                  </Link>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Navbar