import React, { useState } from 'react'
import './usercreate.css'
import { useDispatch, useSelector } from 'react-redux'
import { login, onRegister } from '../redux/thunks'

function Craeteuser() {
  const dispatch = useDispatch()
  const [lemail, setlemail] = useState('')
  const [lpword, setlpword] = useState('')
  const [remail, setremail] = useState('')
  const [rpword, setrpword] = useState('')
  const {errmsglog, errmsgreg} = useSelector((state) => state.login)

  const lobj = {
    email: lemail,
    password: lpword
  }
  const robj = {
    email: remail,
    password: rpword
  }
  function onLogin (e) {
    e.preventDefault();
    dispatch(login(lobj))
  }
  function onRegisterClick (e) {
    e.preventDefault();
    console.log(robj)
    dispatch(onRegister(robj))
  }

  return (
    <div className="usercreate">
      <div className="toplabel">
          Home &gt; User
      </div>
      <div className="row gx-5">
        
        <div className="leftbox col-12 col-md-6">
          <form onSubmit={onLogin}>
            <div className="head">lOGIN</div>
            <label htmlFor="email">Email</label>
            <input type="email" required id='email' onChange={(e) => {setlemail(e.target.value)}} value={lemail}/>
            <label htmlFor="password">Password</label>
            <input type="password" required id='password' onChange={(e) => {setlpword(e.target.value)}} value={lpword}/>
            <div className="errmsg">{errmsglog}</div>
            <button className='login-btn' type='submit'>Login</button>
          </form>
        </div>
        <div className="rightbox col-12 col-md-6">
          <form onSubmit={onRegisterClick}>
            <div className="head">REGISTER</div>
            <label htmlFor="email2">Email</label>
            <input type="email" required id='email2' onChange={(e) => {setremail(e.target.value)}} value={remail}/>
            <label htmlFor="password2">Password</label>
            <input type="password" required minLength="8" id='password2' onChange={(e) => {setrpword(e.target.value)}} value={rpword}/>
            <div className="errmsg">{errmsgreg}</div>
            <button className='login-btn' type='submit'>Register</button>
          </form>
        </div>
      </div>
    </div>
  )
}

export default Craeteuser