import React, { useEffect, useState } from 'react'
import banner1 from './img/banners/side-banner/banner-1.jpg'
import banner2 from './img/banners/side-banner/banner-2.jpg'
import icon1 from './img/banners/side-banner/icon1.svg'
import icon2 from './img/banners/side-banner/icon2.svg'
import icon3 from './img/banners/side-banner/icon3.svg'
import {Link} from 'react-router-dom'

function Sideinfo() {
    const [popularelt, setPopularelt] = useState();
    useEffect(() => {
        fetch("https://fakestoreapi.com/products", {method: "GET",mode: 'cors'}).then ((res) => res.json()).then((json) => {
            setPopularelt(json.slice(15, 19))
        })
    }, [])
    console.log(popularelt)
    if (!popularelt) {
        return (
            <div className="loading">
                <div className="spinner-border text-info" role="status">
                    <span className="sr-only"></span>
                </div>
            </div>
        )
    }
    const renderedElt = popularelt.map((elt) => {
        return (
            <div className="item" key={elt._id}>
                <div className="row">
                    <div className="col-4">
                    <Link to={'/product/'+ elt.id}>
                        <img src={elt.image} alt="" className='w-100'/>
                    </Link>
                    </div>
                    <div className="col-8">
                        <div className="title">
                        <Link to={'/product/'+ elt.id}>
                            {elt.title}
                        </Link>
                        </div>
                        <div className="price">
                            <span className='oldp'><del>$&nbsp;{elt.price + 5}</del></span>
                            <span>$&nbsp;{elt.price}</span>
                        </div>
                    </div>
                </div>
            </div>
        )
    })
  return (
    <div className='side-info'>
        <div className="offers">
            <div><Link to="/">Value of the Day</Link></div>
            <div><Link to="/">Top 100 Offers</Link></div>
            <div><Link to="/">New Arrivals</Link></div>
        </div>
        <div className="banner">
            <img src={banner1} alt="Banner 1" />
            <img src={banner2} alt="Banner 2" />
        </div>
        <div className="details">
            <div className="row p-3 gx-3">
                <div className="col-3"><img src={icon1} alt="icon" /></div>
                <div className="col-9">Download the Bacola App to your Phone.</div>
            </div>
            <div className="row p-3 gx-3">
                <div className="col-3"><img src={icon2} alt="icon" /></div>
                <div className="col-9">Order now so you dont miss the opportunities.</div>
            </div>
            <div className="row p-3 gx-3">
                <div className="col-3"><img src={icon3} alt="icon" /></div>
                <div className="col-9">Your order will arrive at your door in 15 minutes.</div>
            </div>
        </div>
        <div className="popproducts">
            <h4>Trending Products</h4>
            <div className="poplist">
                {renderedElt}
            </div>
        </div>
    </div>
  )
}

export default Sideinfo