import { createAsyncThunk } from "@reduxjs/toolkit";

export const login = createAsyncThunk('login', async(lobj) => {
    console.log('thunk reached' + JSON.stringify(lobj))
    const options = {
        method:'POST',
        body: JSON.stringify(lobj),
        headers: {
            'Content-Type': 'application/json'
            // 'Content-Type': 'application/x-www-form-urlencoded',
        },
        
    }
    const res = await fetch('http://localhost:3000/auth/login',options)
    const data = await res.json();
    console.log(data)
    return data;
})

export const verifyOnMount = createAsyncThunk("verifyOnMount", async(token)=> {
    const options = {
        method:'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': "Bearer"+" "+token
            // 'Content-Type': 'application/x-www-form-urlencoded',
        }
    }
    
    const res = await fetch('http://localhost:3000/auth/verifyOnMount',options)
    const data = await res.json();
    console.log(data)
    return data;
})

export const onRegister = createAsyncThunk('register', async(obj)=> {
    console.log(obj)
    const options = {
        method:'POST',
        headers: {
            'Content-Type': 'application/json',
            // 'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: JSON.stringify(obj)
    }
    const res = await fetch('http://localhost:3000/auth/register',options)
    const data = await res.json();
    console.log(data)
    return data;
})

export const onCartAdd = createAsyncThunk('addCart', async({item, token}) => {
    const options = {
        method:'POST',
        headers: {
            'Content-Type': 'application/json',
            // 'Content-Type': 'application/x-www-form-urlencoded',
            'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(item)
    }
    try {
        const res = await fetch('http://localhost:3000/auth/cartAdd',options)
        const data = await res.json();
        console.log(data)
        return data;
    }
    catch (err) {
        console.log("err caught", err)
    }
})

export const removeCartItem = createAsyncThunk('removeCart', async({id, token}) => {
    console.log(token)
    const options = {
        method:'POST',
        headers: {
            'Content-Type': 'application/json',
            // 'Content-Type': 'application/x-www-form-urlencoded',
            'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({id: id})
    }
    const res = await fetch('http://localhost:3000/auth/cartRemove',options)
    const data = await res.json();
    console.log("removeCart", "thunk finished")
    return data;
})

