import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { useDispatch, useSelector } from 'react-redux';
import { onCartAdd, onfavAdd } from '../redux/thunks';
import Modal from '../Modal'
import { useState } from 'react';
import {Link} from 'react-router-dom'

function Slider_small(props) {
    const dispatch = useDispatch()
    const {isLoggedIn} = useSelector((state) => state.login)
    const [isModalOpen, setIsModalOpen] = useState(false)
    const list = props.list
    const options = {
            dots: false,
            responsive: {
                0: {
                    items: 2,
                },
                600: {
                    items: 3,
                },
                1000: {
                    items: 4,
                }
            }    
        }

    function makeStars(rating) {
        const rounded = Math.round(rating);
        const stars = [...Array(rounded)].map((e, i) => <i className="fa-solid fa-star" key={i}></i>)
        return stars
    }
    function onCartClick (item) {
        if (!isLoggedIn) {
            console.log('notlogged in')
            setIsModalOpen(true)
            return
        }
        console.log('logged in')
        const token = localStorage.getItem('access_token')
        const itemt = {item, token}
        dispatch(onCartAdd(itemt))
    }
    function onFavClick (item) {
        if (!isLoggedIn) {
            console.log('notlogged in')
            setIsModalOpen(true)
            return
        }
        console.log('logged in', item)
        const token = localStorage.getItem('access_token')
        const itemt = {item, token}
        dispatch(onfavAdd(itemt))
        console.log('dispatch')
    }
    const rendereditem = list.map((item, i) => {
        
        return (
            <div className="product-item item border" key={i}>
                <div className="btn-overlay">
                    <span className="heart-con" onClick={() => onFavClick(item)}>
                        <i class="fa-regular fa-heart"></i>
                    </span>
                </div>
                <div className="img-wrapper d-flex justify-content-center">
                <Link to={'/product/'+ item.id}>
                    <img src={item.image} alt="Item's Picture"/>
                </Link>
                </div>
                <div className="details">
                    <Link to={'/product/'+ item.id}>
                        <h4>{item.title}</h4>
                    </Link>
                    <div className='stars-wrapper'>
                        <div className="stars">{makeStars(item.rating.rate)}</div>
                        <div className='reviews'>Out of {item.rating.count} reviews</div>
                    </div>
                    <div className="price">
                        <span className='oldp'><del>$&nbsp;{(item.price + 5).toFixed(2)}</del></span>
                        <span>$&nbsp;{item.price}</span>
                    </div>
                    <div className="btn-wrapper">
                        <button className="cart-btn" onClick={() => onCartClick(item)}>Add to Cart</button>
                    </div>
                </div>
            </div>
        )
    })
    return (
        <>
            <OwlCarousel className='owl-theme small_slider' loop items={4} nav={true}  {...options}>
                {rendereditem}
            </OwlCarousel>
            <Modal isOpen={isModalOpen} message1={'Not Logged In!'} message2={'Please Log In'} button={true} btnMsg={"Login"} onCancel={() => setIsModalOpen(false)}></Modal>
        </>
    )
}

export default Slider_small