import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import {callApi} from '../CallApi'
import Modal from '../Modal';
import { useDispatch, useSelector } from 'react-redux';
import {onCartAdd, onfavAdd} from '../redux/thunks';
import money from '../img/money.svg'
import shipping from '../img/shipping.svg'
import customer from '../img/customer.svg'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import propic1 from '../img/profilePic/procpic(1).jpg'
import propic2 from '../img/profilePic/procpic(2).jpg'
import propic3 from '../img/profilePic/procpic(3).jpg'
import propic4 from '../img/profilePic/procpic(4).jpg'


function Product() {
    const {id} = useParams()
    const [item, setItem] = useState({})
    const dispatch = useDispatch()
    const {isLoggedIn} = useSelector((state) => state.login.status)
    const [isModalOpen, setIsModalOpen] = useState(false)

    useEffect(() => {
        callApi("/products/"+id, setItem)
        window.scrollTo(0, 0);
    }, [id])
    function makeStars(rating) {
        const rounded = Math.round(rating);
        const stars = [...Array(rounded)].map((e, i) => <i className="fa-solid fa-star" key={i}></i>)
        return stars
    }
    function onCartClick (item) {
        if (!isLoggedIn) {
            console.log('notlogged in')
            setIsModalOpen(true)
            return
        }
        console.log('logged in')
        const token = localStorage.getItem('access_token')
        const itemt = {item, token}
        dispatch(onCartAdd(itemt))
    }
    function onFavClick (item) {
        if (!isLoggedIn) {
            console.log('notlogged in')
            setIsModalOpen(true)
            return
        }
        console.log('logged in', item)
        const token = localStorage.getItem('access_token')
        const itemt = {item, token}
        dispatch(onfavAdd(itemt))
        console.log('dispatch')
    }
    const owlOptions = {
        responsiveClass:true,
        responsive:{
            0:{
                items:1,
            },
            850:{
                items:2,
            },
        }
    }

    if (!item.id) {
        return (
            <div className="loading">Loading...</div>
        )
    }
  return (
    <div className="body-wrapper">
        <div className="toplabel">
            Home &gt;&nbsp; Product &gt;&nbsp; {id}
        </div>
        <div className="content-wrapper">
            <div className="top">
                <h2>{item.title}</h2>
                <div className="details">
                    <div className="cato">Category :&nbsp; {item.category}</div>
                    <div className='stars'>{makeStars(item.rating.rate)} out of {item.rating.count} Reviews</div>
                </div>
            </div>
            <div className="row">
                <div className="col-12 col-md-6">
                    <div className="img-wrapper">
                        <img src={item.image} alt="Product's Image" />
                    </div>
                </div>
                <div className="col-12 col-md-6">
                    <div className="price">
                        <del>$ {(item.price).toFixed(2) + 5}</del><span>$ {item.price}</span>
                    </div>
                    <div className="descip">
                        {item.description}
                    </div>
                    <div className="btns">
                        <button onClick={() => onCartClick(item)}>Add to Cart</button>
                        <br />
                        <button onClick={() => onFavClick(item)}>Add to Favorites</button>
                    </div>
                </div>
            </div>
            <div className="reviews">
                <div className="reviews-slider">
                <OwlCarousel className='owl-theme' loop margin={10} {...owlOptions}>
                    <div className='item'>
                        <div className="row">
                            <div className="col-3">
                                <img src={propic1} alt="profile picture" />
                            </div>
                            <div className="col-9">
                                <div className="stars">
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                </div>
                                <div className="text">
                                    <div className="date">May- 1 - 2022</div>
                                    <div className="name">Sarah Marshall</div>
                                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsa incidunt pariatur eveniet expedita corrupti necessitatibus?
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='item'>
                        <div className="row">
                            <div className="col-3">
                                <img src={propic2} alt="profile picture" />
                            </div>
                            <div className="col-9">
                                <div className="stars">
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star blank"></i>
                                    <i class="fa-solid fa-star blank"></i>
                                    <i class="fa-solid fa-star blank"></i>
                                </div>
                                <div className="text">
                                    <div className="date">Feb- 21 - 2022</div>
                                    <div className="name">Maise Kates</div>
                                    Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nam fugiat laborum possimus modi eligendi officia.
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='item'>
                        <div className="row">
                            <div className="col-3">
                                <img src={propic3} alt="profile picture" />
                            </div>
                            <div className="col-9">
                                <div className="stars">
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star blank"></i>
                                    <i class="fa-solid fa-star blank"></i>
                                </div>
                                <div className="text">
                                <div className="date">June- 12 - 2022</div>
                                <div className="name">John Doe</div>
                                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='item'>
                        <div className="row">
                            <div className="col-3">
                                <img src={propic4}   alt="profile picture" />
                            </div>
                            <div className="col-9">
                                <div className="stars">
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star"></i>
                                    <i class="fa-solid fa-star blank"></i>
                                </div>
                                <div className="text">
                                    <div className="date">Feb- 6 - 2022</div>
                                    <div className="name">Chloe Kim</div>
                                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Pariatur ipsam quia voluptatibus animi doloribus nulla.
                                </div>
                            </div>
                        </div>
                    </div>
                    
                </OwlCarousel>;
                </div>
            </div>
            <div className="features">
                <div className='f-item row'>
                    <div className="col-4"><img src={shipping} alt="icon"/></div>
                    <div className="col-8">
                        Free Shipping apply to all orders over $100
                    </div>
                </div>
                <div className='f-item row'>
                    <div className="col-4"><img src={money} alt="icon"/></div>
                    <div className="col-8">
                        1 Day Returns if you change your mind
                    </div>
                </div>
                <div className='f-item row'>
                    <div className="col-4"><img src={customer} alt="icon"/></div>
                    <div className="col-8">
                        Free Shipping apply to all orders over $100
                    </div>
                </div>
            </div>
        </div>
        <Modal isOpen={isModalOpen} message1={'Not Logged In!'} message2={'Please Log In'} button={true} btnMsg={"Login"} onCancel={() => setIsModalOpen(false)}></Modal>
    </div>
  )
}

export default Product