import React from 'react'
import delivery from '../img/shipping.svg'
import money from '../img/money.svg'
import discount from '../img/discount.svg'
import price from '../img/price.svg'
import phone from '../img/phone.svg'
import appstore from '../img/appstore.png'
import playstore from '../img/playstore.png'
import payment from '../img/payments.jpg'

function Footer() {
  return (
    <div className="footer">
      <div className="blue-bar">
        <div className="container">
          <main>
            <div>$20 discount for your first order</div>
            <div>Join our newsletter and get...</div>
            <div>Join our email subscription now to get updates on promotions and coupons.</div>
            <div className="inputbox">
              <i class="bi bi-envelope"></i> <input type="email" placeholder='Your Email Address'/> <button>Subscribe</button>
            </div>
          </main>
        </div>
      </div>
      <main>
        <div className="features-bar">
          <div className="container">
            <div className="item">
              <img src={delivery} alt="icon" />
              <span>Free delivery for order over $70 </span>
            </div>
            <div className="item">
              <img src={discount} alt="icon"/>
              <span>Daily Mega Discounts</span>
            </div>
            <div className="item">
              <img src={price} alt="icon" />
              <span>Best price on the market</span>
            </div>
            <div className="item">
              <img src={money} alt="icon" />
              <span>7-day money-back guarantee</span>
            </div>
          </div>
        </div>
        <div className="container">
          <div className="links">
            <div className="tab">
              <h4>Men's Fashion</h4>
              <ul>
                <li>T-Shirts</li>
                <li>Casual Shirts</li>
                <li>Lightweight Jackets</li>
                <li>Casual</li>
                <li>Joggers &amp; Sweats</li>
                <li>Jeans</li>
                <li>Trunks &amp; Boxers</li>
              </ul>
            </div>
            <div className="tab">
              <h4>Women's Fashion</h4>
              <ul>
                <li>Wedding dress</li>
                <li>Sweater</li>
                <li>T-shirt</li>
                <li>Casual</li>
                <li>Sheath dress</li>
                <li>Dress</li>
                <li>Gym clothes</li>
                <li>Bra</li>
                <li>Tank top</li>
              </ul>
            </div>
            <div className="tab">
              <h4>Men's Fashion</h4>
              <ul>
                <li>T-Shirts</li>
                <li>Casual Shirts</li>
                <li>Lightweight Jackets</li>
                <li>Casual</li>
                <li>Joggers &amp; Sweats</li>
                <li>Jeans</li>
                <li>Trunks &amp; Boxers</li>
              </ul>
            </div>
            <div className="tab">
              <h4>Women's Fashion</h4>
              <ul>
                <li>Wedding dress</li>
                <li>Sweater</li>
                <li>T-shirt</li>
                <li>Casual</li>
                <li>Sheath dress</li>
                <li>Dress</li>
                <li>Gym clothes</li>
                <li>Bra</li>
                <li>Tank top</li>
              </ul>
            </div>
          </div>
        </div>
      </main>
      <footer>
        <div className="container">
          <div className="info-bar">
            <div className="left">
              <div className="img-wrapper">
                <img src={phone} alt="Phone icon" />
              </div>
              <div className="text">
                <h3><a href="tel:8 800 555-55">8 800 555-55</a></h3>
                <span>Working 8:00 - 22:00</span>
              </div>
            </div>
            <div className="right">
              <div className="text">
                <h4>Download App on Mobile :</h4>
                <span>15% discount on your first purchase</span>
              </div>
              <div className="store">
                <img src={playstore} alt="icon store"/>
                <img src={appstore} alt="icon store" />
              </div>
            </div>
          </div>
          <div className="policy-bar">
            <div>Copyright 2022  Bacola WordPress Theme. All rights reserved. Powered by KlbTheme.</div>
            <div>
              <span>Privacy Policy</span>
              <span>Terms and Conditions</span>
              <span>Cookie</span>
            </div>
            <div>
              <img src={payment} alt="payment icon" />
            </div>
          </div>
        </div>
      </footer>
    </div>
  )
}

export default Footer