import { useState } from 'react';
import {useDispatch, useSelector} from 'react-redux'
import Modal from '../Modal';
import { onCartAdd, onfavAdd } from '../redux/thunks';
import {Link} from 'react-router-dom'


function ProductList(props) {
    // const [list, setList] = useState('')

    // useEffect(() => {
    //     callApi("/products", setList)
    // }, [])
    // const callApi = async(endpoint, setState) => {
    //     const baseurl = "https://fakestoreapi.com"
    //     const options = {
    //         method: "GET",
    //         mode: 'cors'
    //     }
    //     const res = await fetch(baseurl+endpoint, options)
    //     const json = await res.json()
    //     console.log(json)
    //     setState(json)
    // }

    // if (!list) {
    //     return (
    //         <div className="loading">
    //             Loading...
    //         </div>
    //     )
    // }
    const list = props.list
    const isLoggedIn = useSelector((state) => state.login.status)
    const [isModalOpen, setIsModalOpen] = useState(false)
    const dispatch = useDispatch()

    function makeStars(rating) {
        const rounded = Math.round(rating);
        const stars = [...Array(rounded)].map((e, i) => <i className="fa-solid fa-star" key={i}></i>)
        return stars
    }
    function onCartClick (item) {
        if (!isLoggedIn) {
            console.log('notlogged in')
            setIsModalOpen(true)
            return
        }
        console.log('logged in')
        const token = localStorage.getItem('access_token')
        const itemt = {item, token}
        dispatch(onCartAdd(itemt))
    }
    function onFavClick (item) {
        if (!isLoggedIn) {
            console.log('notlogged in')
            setIsModalOpen(true)
            return
        }
        console.log('logged in', item)
        const token = localStorage.getItem('access_token')
        const itemt = {item, token}
        dispatch(onfavAdd(itemt))
        console.log('dispatch')
    }

    function renderList (lists) {
        return (
            lists.map((item) => {
                
                return (
                    <div className="product-item border col-6 col-sm-3">
                        <div className="btn-overlay">
                            <span className="heart-con" onClick={() => onFavClick(item)}>
                                <i class="fa-regular fa-heart"></i>
                            </span>
                        </div>
                        <div className="img-wrapper">
                            <Link to={'/product/'+ item.id} className='d-flex justify-content-center h-100'>
                                <img src={item.image} alt="Item's Picture"/>
                            </Link>
                        </div>
                        <div className="details">
                            <Link to={'/product/'+ item.id}>
                                <h4>{item.title}</h4>
                            </Link>
                            <div className='stars-wrapper'>
                                <div className="stars">{makeStars(item.rating.rate)}</div>
                                <div className='reviews'>Out of {item.rating.count} reviews</div>
                            </div>
                            <div className="price">
                                <span className='oldp'><del>$&nbsp;{(item.price+5).toFixed(2)}</del></span>
                                <span>$&nbsp;{item.price}</span>
                            </div>
                            <div className="btn-wrapper">
                                <button className="cart-btn" onClick={() => onCartClick(item)}>Add to Cart</button>
                            </div>
                        </div>
                    </div>
                )
            })
        )
    }
    
    if (list.length < 5) {
        const renderedList = renderList(list)
        return (
            <>
                <div className="ProductList1 row">{renderedList}</div>
            </>
    
        )
    }
        // if list has more than 4 item
        const list1 = list.slice(0, 4)
        const list2 = list.slice(4, list.length)

        
        const renderedList1 = renderList(list1)
        const renderedList2 = renderList(list2)
        console.log(isModalOpen)
    return (
        <>
            <div className="ProductList1 row">{renderedList1}</div>
            {props.banner ? (<div className="awaresness-banner border">
                <div className="banner-txt">
                    <div className="header">Always Wear Mask In Public</div>
                    <div>In Store or online your health &#38; safety is our top priority.</div>
                </div>
            </div>) : ''}
            <div className="ProductList2 row">{renderedList2}</div>
            <Modal isOpen={isModalOpen} message1={'Not Logged In!'} message2={'Please Log In'} button={true} btnMsg={"Login"} onCancel={() => setIsModalOpen(false)}></Modal>
        </>

    )
}

export default ProductList